.block_4 {
    display: flex;
    flex-wrap: wrap;
    padding-top: 150px;
    padding-bottom: 150px;
}

.block_text {
    width: 40%;
    background: #F0F0F0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    padding: 10px;
}

.block_img {
    width: 60%;
    position: relative;
}

.block_img .img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.phone1 {
    position: absolute;
    width: 25%;
    margin-top: -3%;
    margin-right: 45%;
    right: 0;
    top: 0;
}

.phone2 {
    position: absolute;
    width: 15%;
    margin-top: 8%;
    margin-right: 27%;
    right: 0;
    top: 0;
}