.block_3 {
    display: flex;
    flex-wrap: wrap;
}

.block_text {
    width: 40%;
    background: #222222;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    color: white;
    padding: 10px;
}

.block_img {
    width: 60%;
}

.block_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.work {
    display: flex;
    flex-direction: column;
}

.work_text {
    padding-left: 60px;
}

.zag {
    font-size: 17px;
}

.zag img {
    padding: 20px;
}